    #IMPORTS
\*------------------------------------*/
//@import 'partials/all';





/*------------------------------------*\
    #VARIABLES
\*------------------------------------*/

$black: #282828;
$blue: #66a9ff;
$green: #88aa00;
$redwine: #bf3f3f;
$red: #bb3b3b;
$white: #fff;
$dark-grey: #808080;
$light-grey: #f2f2f2;





/*------------------------------------*\
    #GLOBAL
\*------------------------------------*/

body {
  background-color: $white;
  color: black;
  font-family: 'Open Sans', sans-serif;
  margin: 0;
}

a {
  color: $redwine;
  font-weight: bold;
  text-decoration: none;

  &:active,
  &:hover { /* Hack for hide the browser dotted box on click in links */
    outline: 0;
  }
}





/*------------------------------------*\
    #NAVBAR
\*------------------------------------*/

.navbar {
  align-items: center;
  box-sizing: border-box;
  display: flex;
  height: 4rem;
  justify-content: space-between;
  left: 0;
  padding: 0 5%;
  position: fixed;
  right: 0;
  top: 0;
  z-index: 1000;

  &-brand {
  }

  &-logo {
    height: 1.5rem;
    margin-right: .8rem;
    vertical-align: middle;
  }

  &-nav {

  }
}

.nav-link {
  display: inline-block;
  margin-left: 1rem;
  position: relative;
}





/*------------------------------------*\
    #MAIN-GENERAL
\*------------------------------------*/

main {
  height: 100%;
  position: relative;
}


section {
  align-items: center;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: space-around;
  min-height: 100vh;
  max-width: 100vw;
  padding-top: 4rem;
}





/*------------------------------------*\
    #HOME
\*------------------------------------*/

.logo {
  height: 35%;
  max-width: 80%;
  min-width: 35%;
  width: auto;
}

.person {
  text-align: center;

  &>h1 {
    font-family: 'Quicksand', sans-serif;
    font-size: 5vmax;
  }
  &>p {
    color: $redwine;
    font-size: 2vmax;
  }
}

.green {
  color: $green;
}





/*------------------------------------*\
    #ABOUT
\*------------------------------------*/

.about {
  background-color: $light-grey;
  color: $black;
  font-size: 1.8rem;
}

.greeting {
  height: 30%;
  max-width: 80%;
  min-width: 35%;
  width: auto;

  &-paths {
    fill: lighten($red, 5%);
    fill-opacity: 0;
    stroke: $black;
    transition: fill-opacity 1s;

    &.drawn {
      fill-opacity: 1;
    }
  }
}

.emoji {
  width: 2rem;
}

.presentation {
  position: relative;
  width: 32em;

  &>p {
    opacity: 0;
  }
}

$delay: 0;
@for $i from 1 through 6  {
  .phrase#{$i} {
    animation: 1s #{$delay} forwards fadein;
  }
  $delay: $delay + 1.5s;
}

@keyframes fadein {
  0%    { opacity: 0; }
  50%   { opacity: .5; }
  100%  { opacity: 1; }
}





/*------------------------------------*\
    #PROJECTS
\*------------------------------------*/

.projects {
  &>h1 {
    color: $dark-grey;
    font: {
      size: 2.5rem;
      weight: normal;
    }
  }

  &-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
  }
}

.project {
  display: inline-block;
  transition: all .4s ease-out;

  &-img {
    border-radius: 2px;
    box-shadow: 5px 5px 5px $dark-grey;
  }

  &>figcaption {
    text-align: center;
  }

  &:hover {
    transform: rotate(-1deg) scale(1.05);
    transition: all .4s ease-in;
  }
}